import { apiHelper } from './../utils/helpers'
const getToken = localStorage.getItem('token') //tokenkey name

export default {
  //
  signIn({ email, password }) {

    return apiHelper.post('/signin', {
      //returnPromise
      email,
      password
    })
  },

  // signUp({ formData }) {
  //   return apiHelper.post(`/signup`, formData, { //axios data
  //     headers: {
  //       Authorization: `Bearer ${getToken}`
  //     }
  //   })
  // },

  signUp(data) {
    return apiHelper.post(`/signup`,
      { ...data }, { //axios data
      headers: {
        Authorization: `Bearer ${getToken}`
      }
    })
  },

}